.projectWrapper {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
}

.projects {
    width: min-content;
    flex-direction: column;
    justify-content: space-between;
    font-size: 1.1em;
    text-shadow: 1px 1px #ff0000;
    overflow: scroll;
    align-self: center;
    border-radius: 4px;
    padding-left: 50%;
    padding-right: 50%;
    width: 68%;
    height: calc(100vh - 57px);
}

h1 {
    text-shadow: 2px 2px #ff0000;
    text-align: center;
}

.card {
    display: flex;
    justify-content: center;
    flex-direction: row;
    /* height: 100%; */
    /* min-width: 100vw; */
    width: 100%;
}

.col {
    background-color: rgba(53, 46, 46, 0.514);

    /* width: min-content;
    height: min-content; */
    flex: 0 1 100%;
    border-radius: 4px;
    padding: 5px;
    margin: 25px 0 0 0px;
    padding: 15px;
    text-align: center;
    width: 100%;
}

.entry {
    text-align: left;
    padding: 5px;
    transform: scaleX(.79);
    width: 126%;
    position: relative;
    left: -12%;
}

.brainImgs {
    display: flex;
    flex-direction: row;
}

.brainImg {
    display: inline-flex;
    margin: 15px;
    flex: 1 1 220px;
    height: 200px;
    /* min-width: 256px;
    min-height: 256px; */
    /* background-image: url("../../assets/aligned.png") */
    border-radius: 8px;
}

#seg {
    background-position: center;
    background-size: cover;
    background-image: url("../../assets/segmentation.png")
}

#unaligned {
    background-position: center;
    background-size: cover;
    background-image: url("../../assets/unaligned.png")
}

#aligned {
    background-position: center;
    background-size: cover;
    background-image: url("../../assets/aligned.png")
}

.lastImgs {
    display: flex;
    flex-direction: column;
}

.last {
    display: inline-block;
    align-self: center;
    margin: 15px;
    height: auto;
    width: 100%;
    border-radius: 8px;
    /* min-width: 256px;
    min-height: 256px; */
    /* background-image: url("../../assets/aligned.png") */
    padding-top: 51.75%;
}

#homepage {
    background-position: center;
    background-size: cover;
    background-image: url("../../assets/homepage.png")
}

#albumpage {
    background-position: center;
    background-size: cover;
    background-image: url("../../assets/albumpage.png")
}

