    display: inline-flex;
    width: 100%;
    height: 100%;
    flex: 3 1 100%;
} */

.video {
    display: inline-block;
    margin-left: auto;
    margin-right: auto;
    min-height: fit-content;
    height: 300px;
    width: 100%;
    max-width: 400px;
}