.bodyText {
    display: flex;
    flex-direction: column;
    margin: auto 10% auto 10%;
    padding: 15px 0 15px 0;
    top: 50vh;
    align-items: center;
    justify-content: center;
    background-color: rgba(53, 46, 46, 0.514);
    /* background-color: rgba(0, 0, 0, 0.747); */
    border-radius: 4px;
    max-height: min-content;
    white-space: nowrap;
    max-width: 75%;

}

.links {
    display: flex;
    flex-direction: row;
    justify-content: center;
    text-align: center;
    color: rgb(36, 14, 14);
}

.link {
    padding: 1% 2% 1% 2%;
    white-space: nowrap;
    text-shadow: 1px 1px #ff0000;
    font-size: 1.2em;
    transform: scaleX(.79);
    width: max-content;

}



.header {
    padding: 1%;
    text-shadow: 2px 2px #ff0000;
    text-align: center;
    padding: 0;
    margin: 0;
    font-size: 3.1em;
    transform: scaleX(.79);
    white-space: normal;
}

.description {
    padding: 1%;
    font-size: 1.6em;
    font-style: italic;
    text-shadow: 1px 1px #ff0000;
    transform: scaleX(.79);
}

#small {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

#small p {
    margin: 0;
}