.top_links {
    display: flex;
    flex-direction: row;
    justify-content: center;
    height: 32px;
    width: 100%;
    background-color: rgba(53, 46, 46, 0.514);
    margin-top: 25px;
    position: absolute;
    bottom: 0;
}

.outer {
    padding: .5% 2% .5% 2%;
    white-space: nowrap;
    /* text-shadow: 1px 1px #ff0000; */
    font-size: 1.2em;
}

.inner {
    width: 100%;
    height: 100%;
    transform: scaleX(.79);
    font-style: italic;
}

.top_links div a {
    text-decoration: none;
    color: inherit;
    text-shadow: 1px 1px #ff0000;
}

.outer:hover {
    background-color: rgba(27, 26, 26, 0.856) !important;
}