.social {
    padding: 2px;
    margin: 0 auto;
    display: flex;
    bottom: 0;
    z-index: 1;
}

.stack {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-items: stretch;
    text-decoration: none;
}

.stack:visited {
    color: inherit;
}

.icon {
    transform: translateY(-2px);
    width: 35px;
    height: 35px;
    margin-right: 20px;
    border-radius: 20px;
    /* filter: invert(100%) grayscale(100%) brightness(65%) sepia(100%) hue-rotate(295deg); */
    /* saturate(350%) contrast(1); */
    box-shadow: 1px 1px #ff0000;
}


.label {
    display: none;
    padding-right: 0px;
    font-size: 1.2em;
}