.wrapper {
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.contactInfo {
    max-width: 80%;
    font-size: 1.4em;
    position: relative;
    text-align: left;
    text-shadow: 1px 1px #ff0000;
    background-color: rgba(53, 46, 46, 0.788);
    /* background-color: rgba(53, 46, 46, 1); */
    padding: 10px;
    border-radius: 5px;
}

.spacer {
    flex-grow: 1;
}

.value {
    text-align: right;
    align-self: flex-end;
    padding: 0px 0px 0px 30px;
    font-style: italic;
}

.row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 5px 0px;
    transform: scaleX(.79);
    mix-blend-mode: normal;
}

#address {
    align-items: center;
}